.detail{
    width: 100%;
    height: 700px;
    display: flex;
}
.detail-img{
    width: 45%;
}
.detail-img img{
    width: 100%;
    height: 700px;
}
.detail-info{
    background-color:rgba( 150, 111, 51, 0.5);
    color: #1E2A47;
    font: italic;
    font-weight: 700;
    font-family: 'fira sans';
    font-size: clamp(16px, 2vw,  32px);
    width: 55%;
    display: flex;
    flex-direction: column;
    padding: 1%;
}
.detail-info span{
    margin-top: 1em;
}
.individual-info{
    color: rgb(247, 239, 239);
}