.product-card{
    width: clamp(300px, 20%, 350px);
    display: flex;
    flex-direction: column;
    min-height: max-content;
    align-items: center;
    border-radius: 8px;
    margin-block: 15px;
    padding: 1%;
    background-color: rgba( 150, 111, 51, 0.5);
}

.cart-btns{
    display: flex;
    justify-content: space-between;
}

.item-amount{
    width: 20%;
    outline: transparent;
}

.item-amount, select, textarea{
    font-family: 'fira sans';
}

.add-to-cart{
    background: rgb(247, 239, 239);
    border-radius: 8px;
    border: transparent;
    padding: 2%;
    color: #1E2A47;
    font-weight: 700;
    font-family: 'fira sans';
}

.add-to-cart:hover{
    background: #1E2A47;
    color: whitesmoke;
    cursor: pointer;
}

.details{
    background: transparent;
    border: transparent;
    color: #1E2A47;
    font-weight: 700;
    font-family: 'fira sans';
    transition: border 0.4s;
    border-bottom: 1px, solid, transparent;
    padding-bottom: 3px;
    height: 1.5em;
    font-size: 0.7rem;
}


.details:hover{
    border-bottom: 1px solid #1E2A47;
    cursor: pointer;
}

.product-img{
    width: 100%;
    height: 100px;
    background-size:100% 100%;
    margin-bottom: 5%;
}
.product-info{
    width: 100%;
    color: #10182c;
    font-weight: 800;
    font-size: 1.3rem;
}
.product-info div{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.name{
    width: 100%;
    min-height: 5em;
    margin-bottom: 3%;
    line-height: 1em;
}
