.menu{
    width: 100%;
    background: url('./imgs/bbb.jpg');
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
    gap: 4%;
}
.darkening-effect{
    background-color: rgb(7, 7, 7, 0.6) ;
    padding-inline: 10%;
}

.food-categories{
    display: flex;
    flex-direction: column;
}
.food-categories-card{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 4%;
}

.food-categories-text{
    text-align: center;
    margin-block: 2em;
    font-weight: 800;
    font-size: clamp(18px, 2vw, 40px);
    color: aliceblue;
}

@media only screen and (max-width: 660px){
   .food-categories-card{
    flex-direction: column;
    align-items: center;
   }
}