header{
    width: 100%;
    padding: 1%;
    background-color: rgb(247, 239, 239);
}
header nav{
    display: flex;
    justify-content: space-between;
}
header ul{
    width: 70%;
    display: flex;
    justify-content: space-around;
}
header li{
    font-size: larger;
    font-weight: 700;
    color: #1E2A47;
    padding: 2%;
    border-radius: 7px;
    transition: background 0.5s;
    
}
header a{
    transition: color 0.5s;
    display: flex;
    align-items: center;
}
header li:hover{
   background:#1E2A47;
}
header li:hover  a{
    color: whitesmoke;
}

.cart-icon{
    width: 40px;
    height: 20px;
    display: flex;
    justify-content: center;
    transition-property: width, height, color, ;
    transition-duration: 0.3s;
    border-bottom: 1px solid transparent;
}

.cart-icon:hover{
    width: 50px;
    height: 25px;
    border-bottom: 1px solid #1E2A47;
    color: red;
    font-weight: 700;
}

.dropdown{
    position: relative;
}

.dropdown-content{
    background-color: rgb(30, 42, 71);
    padding-inline: 30%;
    padding-block: 5%;
    border-radius: 9px;
    position: absolute;
    display: none;
    z-index: 1;
    overflow: visible;
    width: padding;
    white-space: noWrap;
    width: fit-content;
    left: -70%;
}

.dropdown-content p{
    margin-block: 0.5em;
}

.dropdown-content p:hover{
    color: red;
}

#menu-active{
    display: block;
}