.cart-content{
    width: 100%;
    padding-inline: 10%;
    display: flex;
    flex-wrap: wrap;
    gap: 4%;
}
.cart{
    margin-bottom: auto;
    
}
.cart-header{
    padding-top: 0.5em;
    font-size:clamp(16px, 1.5vw, 32px);
    display: flex;
    justify-content: center;
}
.total-price{
   margin-right: 3%;
}
.clear-cart{
    cursor: pointer;
    font-weight: 700;
    font-style: normal;
    line-height: 1em;
    transition: border 0.4s;
    border-bottom: 2px solid transparent;
    height: 1em;
}

.clear-cart:hover{
    border-bottom: 2px solid red;
    color: red;
}