.location{
    width: clamp(290px, 50vw, 1200px);
    height: clamp(290px, 50vw, 1200px)
}
.contact-us{
    display: flex;
    font-size: larger;
    font-weight: 700;
    padding-block: 5%;
}
.contact-us p{
    margin-bottom: 1rem;
    text-align: start;
    margin-left: 5vw;
    color: #1E2A47;
}

@media only screen and (max-width: 660px){
   .contact-us{
    flex-direction: column-reverse;
    align-items: center;
   }
}