.home{
    width: 100%;
    height: 100%;
    padding-block: clamp();
    background-size:100% 100%;
    transition: background 0.25s;
}

.glass-effect{
    background: rgba(212, 175, 55, 0.3);
    width: 100%;
    height: 100%;
    padding-bottom: 1%;
}
.welcome{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.welcome h2{
    margin-top: 0.5em;
    font-size: 2rem;
    color:  #1E2A47;
}

.resto-name{
    margin-top: 1em;
    font-size: 3rem;
    font-weight: 700;
    color:  #1E2A47;
}

.popular-dishes{
    margin-inline:5%;
    margin-top: 15%;
}
.popular-dishes h3{
    font-size: 35px;
    font-weight: 500;
    color:  #1E2A47;
}
.popular-dish-cards{
    display: flex;
    justify-content: space-between;
}

@media only screen and (max-width: 660px){
    .popular-dishes{
        text-align: center;
    }
    .popular-dish-cards{
        flex-direction: column;
        align-items: center;
    }
}